import React from 'react';
import { useThemeContext } from '@aave/aave-ui-kit';

import Link from '../../../../basic/Link';
import Value from '../../../../basic/Value';
import Row from '../../../../basic/Row';
import { getAssetInfo, TokenIcon } from '../../../../../helpers/config/assets-config';

import staticStyles from './style';
import theme from '../../../../../theme';

interface CardProps {
  link: string;
  symbol: string;
  id: string;
  value: string;
  underlyingAsset: string;
}

export default function Card({ link, symbol, id, value, underlyingAsset }: CardProps) {
  const { currentTheme, xl, sm } = useThemeContext();

  const asset = getAssetInfo(symbol);

  const iconSize = xl && !sm ? 24 : sm ? 24 : 24;

  return (
    <Link className="Card ButtonLink" to={link} color="dark">
      <Row className="Card__content">
        <TokenIcon
          tokenSymbol={symbol}
          height={iconSize}
          width={iconSize}
          tokenFullName={asset.shortSymbol || asset.formattedName}
        />
        <Value
          value={value}
          maximumValueDecimals={6}
          tooltipId={`${underlyingAsset}-${id}`}
          maximumSubValueDecimals={2}
        />
      </Row>

      <style jsx={true} global={true}>
        {staticStyles}
      </style>
      <style jsx={true} global={true}>{`
        .Card {
          transition: 0.1s ease-in-out;
          &:hover {
            // border-left: 2px solid ${theme.color.primary[300]};
            background: linear-gradient(
              90deg,
              transparent,
              rgba(255, 255, 255, 0.05) 60%,
              transparent
            );
          }
          &__content {
            padding-top: 12px;
            padding-bottom: 12px;
            border-bottom: 1px solid ${theme.color.transparent[500]};
          }
        }
      `}</style>
    </Link>
  );
}
