import React from 'react';

import TableItem from '../../../../components/BasicAssetsTable/TableItem';
import TableColumn from '../../../../components/BasicTable/TableColumn';
import Value from '../../../../components/basic/Value';
import LiquidityMiningCard from '../../../../components/liquidityMining/LiquidityMiningCard';
import NoData from '../../../../components/basic/NoData';
import { getAssetInfo, isAssetStable } from '../../../../helpers/config/assets-config';

import { DepositTableItem } from './types';
import staticStyles from './style';
import TokenIcon from '../../../../components/TokenIcon';

export default function DepositItem({
  id,
  symbol,
  underlyingAsset,
  walletBalance,
  walletBalanceInUSD,
  liquidityRate,
  avg30DaysLiquidityRate,
  userId,
  borrowingEnabled,
  isFreezed,
  aincentivesAPR,
}: DepositTableItem) {
  const url = `/deposit/${underlyingAsset}-${id}`;

  return (
    <TableItem symbol={symbol} url={url} isFreezed={isFreezed} darkOnDarkMode={true}>
      {/*showing wallet balance*/}
      <TableColumn>
        {!userId || Number(walletBalance) <= 0 ? (
          <NoData color="dark" />
        ) : (
          <Value
            value={Number(walletBalance)}
            subValue={walletBalanceInUSD}
            maximumSubValueDecimals={2}
            subSymbol="USD"
            maximumValueDecimals={isAssetStable(symbol) ? 2 : 5}
            minimumValueDecimals={isAssetStable(symbol) ? 2 : 5}
          />
        )}
      </TableColumn>

      {/*showing apr*/}
      {!isFreezed && (
        <TableColumn className={'ToolTip'}>
          <div className={'ToolTip__Value'} onMouseOver={() => {}}>
            <div className={'ToolTip__Body'}>
              <div className={'ToolTip__content'}>{liquidityRate}</div>
            </div>
            <div className={'single-line-center-start'}>
              <LiquidityMiningCard
                value={liquidityRate}
                thirtyDaysValue={avg30DaysLiquidityRate}
                liquidityMiningValue={aincentivesAPR}
                symbol={symbol}
                type="deposit"
              />
              <TokenIcon
                tokenSymbol={'ARTH'}
                height={16}
                width={16}
                getAssetInfo={getAssetInfo}
                className={'m-l-4'}
              />
            </div>
          </div>
        </TableColumn>
      )}
      <style jsx={true} global={true}>
        {staticStyles}
      </style>
    </TableItem>
  );
}
